import React from "react";
import { parse, stringify } from "query-string";
import { Navigation } from "../../lib";
import FPWDeal from "./FPWDeal";
import Modal from "react-modal";
import Button from "muicss/lib/react/button";
import Expire from "../common/Expire";
import LazyLoad from "react-lazyload";

class FPWDealPage extends React.Component {
  constructor(props) {
    super(props);
    this.compsClick = this.compsClick.bind(this);
  }

  dealClick(dealType, dealId, violations, additional_properties, page) {
    this.props.setPageAndScrollPos("/broker-deals/fpw", window.scrollY);
    Navigation.go(
      `/broker-deal/${dealType}/${dealId}/${page}?${stringify({
        ...parse(window.location.search),
        violations,
        additional_properties,
        previous_page: window.location.pathname + window.location.search,
      })}`
    );
  }

  addDealNotesClick(dealType, dealId, violations, additional_properties, e) {
    e.stopPropagation();
    Navigation.go(
      `/broker-deal/${dealType}/${dealId}/notes/add?${stringify({
        violations,
        additional_properties,
      })}`
    );
  }

  compsClick(inNYC, propertyAddress, city, property_type_id, e) {
    e.stopPropagation();
    sessionStorage.setItem("comps_inNYC", inNYC);
    this.props.setPageAndScrollPos("/broker-deals/fpw", window.scrollY);
    this.props.submitCompsSearch(
      `${propertyAddress} ${city}`,
      property_type_id
    );
  }

  render() {
    let successMessage = "";
    if (this.props.toast.show) {
      successMessage = (
        <Expire closeToast={this.props.closeToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.toast.message}</p>
            <Button variant="flat" onClick={this.props.closeToast}>
              {this.props.toast.success ? "Done" : "Dismiss"}
            </Button>
          </div>
        </Expire>
      );
    }

    var counter = 0;
    return (
      <div>
        <div>
          {this.props.suggestions &&
            this.props.suggestions.map((item, index) => (
              <LazyLoad height={200} offset={100} once key={index}>
                <FPWDeal
                  key={counter++}
                  deal={item}
                  contact={this.props.contact}
                  changeExpectedIPDate={this.props.changeExpectedIPDate}
                  updateStar={this.props.updateStar}
                  updateDealReview={this.props.updateDealReview}
                  clickFunction={this.dealClick.bind(this)}
                  loadingContact={this.props.loadingContact}
                  addDealNoteClick={this.addDealNotesClick.bind(this)}
                  compsClick={this.compsClick}
                  updateRating={this.props.updateRating}
                  getFullContactInfo={this.props.getFullContactInfo}
                />
              </LazyLoad>
            ))}
          ;{successMessage}
        </div>
        <Modal
          className="loading-modal"
          ariaHideApp={false}
          contentLabel="Modal"
          isOpen={this.props.loading}
          shouldCloseOnOverlayClick={false}
        >
          <div className={this.props.loading ? "centered-spinner" : "hidden"}>
            <img
              src="/images/icons/spin.svg"
              className="loading-spinner"
              alt="loading spinner"
            />
          </div>
        </Modal>
        <div
          className={
            !this.props.loading &&
            (this.props.noFPWDeals ||
              (this.props.suggestions && this.props.suggestions.length < 1))
              ? ""
              : "hidden"
          }
        >
          <div>
            <div className="grey-circle">
              <i className="material-icons">warning</i>
            </div>
            <h2 className="no-results">No FPW Deals</h2>
          </div>
        </div>
      </div>
    );
  }
}
export default FPWDealPage;
