import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import FPWDealPage from "./FPWDealPage";
import * as dealsActions from "../../actions/dealsActions";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import * as compsActions from "../../actions/compsActions";
import { getObjectKeyByValue } from "../../lib/utils";
import { DealStatusType } from "../../lib/constants";

const dealType = getObjectKeyByValue(DealStatusType, DealStatusType.FPW);

class FPWDealContainer extends React.Component {
  constructor(props) {
    super(props);
    this.updateStar = this.updateStar.bind(this);
    this.updateSearch = this.updateSearch.bind(this);
    this.changeExpectedIPDate = this.changeExpectedIPDate.bind(this);
    this.updateDealReview = this.updateDealReview.bind(this);
  }

  componentDidMount() {
    if (
      this.props.comps.previousScrollPos &&
      this.props.comps.previousScrollPos.length
    ) {
      window.scrollTo(
        0,
        this.props.comps.previousScrollPos[
          this.props.comps.previousScrollPos.length - 1
        ]
      );
    }
    this.props.compsActions.resetScrollPosition();
    this.props.dealsActions.loadFPWDeals();
  }

  updateStar(deal_id, broker_starred, e) {
    e.stopPropagation();
    this.props.dealsActions.updateStar(deal_id, broker_starred, dealType);
  }

  updateSearch(suggestions) {
    this.props.dealsActions.updateSearch(suggestions, dealType);
  }

  changeExpectedIPDate(deal_id, date) {
    this.props.dealsActions.updateExpectedIPDate(deal_id, date, dealType);
  }

  updateDealReview(
    deal_id,
    forecast_reviewed,
    ip_date_reviewed,
    notes_reviewed
  ) {
    this.props.dealsActions.updateDealReviewed(
      deal_id,
      forecast_reviewed,
      ip_date_reviewed,
      notes_reviewed
    );
  }

  updateRating(deal_id, deal_rating) {
    this.props.dealsActions.updateRating(deal_id, deal_rating, dealType);
  }

  render() {
    return (
      <FPWDealPage
        suggestions={this.props.fpwDeals.suggestions}
        contact={this.props.fpwDeals.contact}
        updateSearch={this.updateSearch}
        loading={this.props.fpwDeals.loading}
        loadingContact={this.props.fpwDeals.loadingContact}
        noDeals={this.props.fpwDeals.noFPWDeals}
        updateStar={this.updateStar}
        changeExpectedIPDate={this.changeExpectedIPDate}
        closeToast={this.props.dealsActions.closeToast}
        toast={this.props.fpwDeals.toast}
        updateDealReview={this.updateDealReview}
        updateRating={this.updateRating.bind(this)}
        getFullContactInfo={this.props.dealsActions.getFullContactInfo}
        setPageAndScrollPos={this.props.compsActions.setPageAndScrollPos}
        submitCompsSearch={this.props.compsActions.submitCompsSearch}
      />
    );
  }
}

function mapStateToProps(state) {
  return {
    fpwDeals: state.fpwDeals,
    comps: state.comps,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    dealsActions: bindActionCreators(dealsActions, dispatch),
    navigationTitleActions: bindActionCreators(
      navigationTitleActions,
      dispatch
    ),
    compsActions: bindActionCreators(compsActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(FPWDealContainer);
