import React from "react";
import Modal from "react-modal";
import Form from "muicss/lib/react/form";
import Button from "muicss/lib/react/button";

export default class DealReviewModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      check_all: false,
      forecast_reviewed: null,
      ip_date_reviewed: null,
      notes_reviewed: null,
    };
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    this.setState({
      check_all: nextProps.allReviewed,
      forecast_reviewed: nextProps.forecast_reviewed,
      ip_date_reviewed: nextProps.ip_date_reviewed,
      notes_reviewed: nextProps.notes_reviewed,
    });
  }

  checkHandler(e) {
    e.stopPropagation();
    if (e.target.id === "check_all") {
      if (e.target.checked) {
        //check all checkboxes
        this.setState({
          check_all: true,
          forecast_reviewed:
            this.props.forecast_reviewed === null
              ? "touched"
              : this.props.forecast_reviewed,
          ip_date_reviewed:
            this.props.ip_date_reviewed === null
              ? "touched"
              : this.props.ip_date_reviewed,
          notes_reviewed:
            this.props.notes_reviewed === null
              ? "touched"
              : this.props.notes_reviewed,
        });
      } else {
        //uncheck all checkboxes
        this.setState({
          check_all: false,
          forecast_reviewed:
            this.props.forecast_reviewed != null
              ? this.props.forecast_reviewed
              : null,
          ip_date_reviewed:
            this.props.ip_date_reviewed != null
              ? this.props.ip_date_reviewed
              : null,
          notes_reviewed:
            this.props.notes_reviewed != null
              ? this.props.notes_reviewed
              : null,
        });
      }
    } else {
      this.setState(
        { [e.target.id]: e.target.checked === true ? "touched" : null },
        () => {
          let checkAll =
            this.state.forecast_reviewed != null &&
            this.state.ip_date_reviewed != null &&
            this.state.notes_reviewed != null;
          this.setState({ check_all: checkAll });
        }
      );
    }
  }

  updateDealReview(e) {
    this.props.updateDealReview(
      this.state.forecast_reviewed,
      this.state.ip_date_reviewed,
      this.state.notes_reviewed
    );
    this.props.onRequestClose(e);
  }

  cancel(e) {
    this.setState({
      check_all: false,
      forecast_reviewed: this.props.forecast_reviewed,
      ip_date_reviewed: this.props.ip_date_reviewed,
      notes_reviewed: this.props.notes_reviewed,
    });

    this.props.onRequestClose(e);
  }

  render() {
    return (
      <Modal
        className="deal-rating-modal deal-review-modal"
        ariaHideApp={false}
        contentLabel="Modal"
        isOpen={this.props.isOpen}
        onRequestClose={(e) => this.props.onRequestClose(e)}
        onAfterOpen={this.props.onAfterOpen}
        shouldCloseOnOverlayClick={true}
      >
        <Form>
          <i
            className="material-icons modal-exit"
            onClick={(e) => this.props.onRequestClose(e)}
          >
            close
          </i>
          <h4 className="change-rating-header">Review</h4>
          <div className="rkmd-checkbox checkbox-ripple">
            <label className="input-checkbox checkbox-lightBlue">
              <input
                type="checkbox"
                id="check_all"
                checked={this.state.check_all}
                onChange={(e) => this.checkHandler(e)}
                disabled={this.props.allReviewed}
              />
              <span className="checkbox" />
              <span className="ripple" />
            </label>
            <p htmlFor="check_all" className="label">
              Check All
            </p>
          </div>
          <div className="rkmd-checkbox checkbox-ripple">
            <label className="input-checkbox checkbox-lightBlue">
              <input
                type="checkbox"
                id="forecast_reviewed"
                checked={this.state.forecast_reviewed != null}
                onChange={(e) => this.checkHandler(e)}
                disabled={this.props.forecast_reviewed != null}
              />
              <span className="checkbox" />
              <span className="ripple" />
            </label>
            <p htmlFor="forecast_reviewed" className="label">
              Forecast
            </p>
            <p className="bold-text review-modal-detail">
              {this.props.forecast}
            </p>
          </div>
          <div className="rkmd-checkbox checkbox-ripple">
            <label className="input-checkbox checkbox-lightBlue">
              <input
                type="checkbox"
                id="ip_date_reviewed"
                checked={this.state.ip_date_reviewed != null}
                onChange={(e) => this.checkHandler(e)}
                disabled={this.props.ip_date_reviewed != null}
              />
              <span className="checkbox" />
              <span className="ripple" />
            </label>
            <p htmlFor="ip_date_reviewed" className="label">
              Expected IP Date
            </p>
            <p className="bold-text review-modal-detail">
              {this.props.ip_date}
            </p>
          </div>
          <div className="rkmd-checkbox checkbox-ripple">
            <label className="input-checkbox checkbox-lightBlue">
              <input
                type="checkbox"
                id="notes_reviewed"
                checked={this.state.notes_reviewed != null}
                onChange={(e) => this.checkHandler(e)}
                disabled={this.props.notes_reviewed != null}
              />
              <span className="checkbox" />
              <span className="ripple" />
            </label>
            <p htmlFor="notes_reviewed" className="label">
              Last Updated Note
            </p>
            <p className="review-modal-note">{this.props.notes}</p>
          </div>
          <div className="modal-btns">
            <Button variant="flat" onClick={(e) => this.cancel(e)}>
              Cancel
            </Button>
            <Button
              variant="flat"
              onClick={(e) => this.updateDealReview(e)}
              disabled={this.props.allReviewed}
            >
              Save
            </Button>
          </div>
        </Form>
      </Modal>
    );
  }
}
