import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Divider from "muicss/lib/react/divider";
import ClientModal from "../common/ClientModal";
import DealRatingModal from "../common/DealRatingModal";
import DealReviewModal from "../common/DealReviewModal";
import moment from "moment";
import { formatTotalComps, isEmpty } from "../../lib";
import { DealStatusType } from "../../lib/constants";
import getParameterByName from "../common/getParameterByName";

class FPWDeal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modalOpen: false,
      modalRatingOpen: false,
      modalReviewOpen: false,
      datePickerOpen: false,
      showMoreBrokers: false,
    };

    this.ref = React.createRef();

    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.openRatingModal = this.openRatingModal.bind(this);
    this.updateRating = this.updateRating.bind(this);

    this.openReviewModal = this.openReviewModal.bind(this);
    this.closeReviewModal = this.closeReviewModal.bind(this);
    this.updateDealReview = this.updateDealReview.bind(this);
  }

  componentDidMount() {
    if (this.props.deal.deal_id === getParameterByName("deal_id")) {
      this.ref.current.scrollIntoView({
        block: "center",
        inline: "center",
      });
    }
  }

  async updateRating(deal_rating, e) {
    e.stopPropagation();
    this.setState({ modalRatingOpen: false });
    await this.props.updateRating(this.props.deal.deal_id, deal_rating);
    this.updateDealReview(
      "updated",
      this.props.deal.ip_date_reviewed,
      this.props.deal.notes_reviewed
    );
  }

  openModal(e) {
    e.stopPropagation();
    if (this.props.deal.contact_id) {
      this.props.getFullContactInfo(this.props.deal.contact_id);
      this.setState({
        modalOpen: true,
      });
    }
  }

  openRatingModal(e) {
    e.stopPropagation();
    this.setState({
      modalRatingOpen: true,
    });
  }

  getRatingColor(rating) {
    if (rating === 0) {
      return "circle-background green-circle";
    }
    if (rating === 1) {
      return "circle-background yellow-circle";
    }
    if (rating === 2) {
      return "circle-background pink-circle";
    }
    if (rating === 3) {
      return "circle-background orange-circle";
    }
  }

  afterOpenModal() {}

  closeModal(e) {
    e.stopPropagation();
    this.setState({ modalOpen: false });
  }

  openReviewModal(e) {
    e.stopPropagation();
    this.setState({ modalReviewOpen: true });
  }

  closeReviewModal(e) {
    e.stopPropagation();
    this.setState({ modalReviewOpen: false });
  }

  updateDealReview(forecast_reviewed, ip_date_reviewed, notes_reviewed) {
    if (this.props.deal.qts_placer) {
      this.props.updateDealReview(
        this.props.deal.deal_id,
        forecast_reviewed,
        ip_date_reviewed,
        notes_reviewed
      );
    }
  }

  changeExpectedIPDate(date) {
    this.props.changeExpectedIPDate(this.props.deal.deal_id, date);
    this.updateDealReview(
      this.props.deal.forecast_reviewed,
      "updated",
      this.props.deal.notes_reviewed
    );
  }

  showDatePicker(e) {
    e.stopPropagation();
    this.setState({ datePickerOpen: true });
    // this.changeExpectedIPDate(moment().format("YYYY-MM-DD"));
  }

  showMoreBrokersToggle(e) {
    e.stopPropagation();
    this.setState({ showMoreBrokers: !this.state.showMoreBrokers });
  }

  dealClick = (e, page = "property-details") => {
    e.stopPropagation();
    const className = e.target.offsetParent.className;
    if (
      !className.includes("Modal") &&
      !className.includes("react-date-picker") &&
      !className.includes("checkbox")
    ) {
      const {
        status_string,
        deal_id,
        total_violations,
        additional_properties,
      } = this.props.deal;
      this.props.clickFunction(
        status_string || "FPW",
        deal_id,
        total_violations,
        additional_properties,
        page
      );
    }
  };

  render() {
    const {
      deal: {
        deal_id,
        deal_status,
        status_string,
        property_type_id,
        broker_starred,
        deal_name,
        city_state,
        property_type,
        property_address,
        additional_properties,
        full_name,
        contact_id,
        expected_inprocess_date,
        loan_amount,
        brokers,
        notes,
        last_notes_by,
        notes_date,
        notes_due_date,
        note_count,
        deal_rating,
        forecast_reviewed,
        ip_date_reviewed,
        notes_reviewed,
        qts_placer,
        TotalComps,
        total_complaints,
        total_violations,
        inNYC,
      },
      contact,
      loadingContact,
      isSearchPage,
    } = this.props;
    var originators = "";
    var brokersList = "";
    let originatorsCounter = 0;
    let brokersCounter = 0;
    brokers &&
      brokers.forEach((broker) => {
        if (broker.broker_name) {
          if (broker.broker_roles.includes("Originator")) {
            if (originatorsCounter >= 1) {
              originators += ", ";
            }
            originators += broker.broker_name;
            originatorsCounter++;
          } else {
            if (brokersCounter >= 1) {
              brokersList += ", ";
            }
            brokersList += broker.broker_name;
            brokersCounter++;
          }
        }
      });
    return (
      <div ref={this.ref} className="fpw-deal">
        <div className="panel mui-panel" onClick={(e) => this.dealClick(e)}>
          <Row className="panel-heading">
            <Col md="10" sm="10" xs="9" key={deal_id}>
              <div className="deal-name">
                <p className="panel-info-value property-title">
                  <b>
                    <span>{deal_name || ""}</span>
                  </b>
                  <span className="deal-id">ID {deal_id}</span>
                  {(!isSearchPage || deal_status === DealStatusType.FPW) && (
                    <span
                      onClick={(e) => this.openRatingModal(e)}
                      className={this.getRatingColor.bind(this)(deal_rating)}
                    >
                      {deal_rating}
                    </span>
                  )}
                  {isSearchPage && (
                    <span className="deal-status-tag">{status_string}</span>
                  )}
                </p>
              </div>
            </Col>
            <Col md="3" sm="2" xs="3">
              <div
                onClick={(e) =>
                  this.props.updateStar(deal_id, broker_starred, e)
                }
                className="deal-star"
              >
                <p>
                  {broker_starred === 1 ? (
                    <span className="ip-quote-star">
                      <i className="material-icons star-icon-orange">star</i>
                    </span>
                  ) : (
                    <span className="ip-quote-star">
                      <i className="material-icons star-icon-gray">star</i>
                    </span>
                  )}
                </p>
              </div>
              {!isSearchPage && qts_placer && (
                <div
                  className="r-icon"
                  onClick={(e) => this.openReviewModal(e)}
                >
                  {forecast_reviewed != null &&
                  ip_date_reviewed != null &&
                  notes_reviewed != null ? (
                    <img
                      src="/images/icons/review-icon-green.png"
                      alt="review-icon-green"
                    />
                  ) : (
                    <img
                      src="/images/icons/review-icon-gray.png"
                      alt="review-icon-gray"
                    />
                  )}
                </div>
              )}
            </Col>
          </Row>
          <Row className="panel-body">
            <Row>
              <Col md="3">
                <div>
                  <p className="small-title deal-info-title">Address</p>
                  <p className="panel-info-value">
                    {property_address ? property_address : ""}
                  </p>
                  <p className="panel-info-value">{city_state || ""}</p>
                </div>
                <div>
                  <p className="panel-info-value">
                    <i className="orange-text orange-text-hover pointer">
                      {additional_properties === 0
                        ? null
                        : additional_properties === 1
                        ? "1 other property"
                        : additional_properties + " other properties"}
                    </i>
                  </p>
                </div>
              </Col>
              <Col onClick={(e) => this.openModal(e)} md="3">
                <p className="small-title deal-info-title">Name of Client</p>
                <p
                  className={
                    contact_id
                      ? "panel-info-value client-name"
                      : "panel-info-value"
                  }
                >
                  {full_name || "N/A"}
                </p>
              </Col>
              {/*<Col md="3">*/}
                {/*<p className="small-title deal-info-title">Expected IP Date</p>*/}
                {/*<div className="expected-ip">*/}
                  {/*{deal_status === DealStatusType.CL ? (*/}
                    {/*<p className="panel-info-value client-name">*/}
                      {/*{expected_inprocess_date || "N/A"}*/}
                    {/*</p>*/}
                  {/*) : (expected_inprocess_date !== null &&*/}
                      {/*moment(expected_inprocess_date).isValid()) ||*/}
                    {/*this.state.datePickerOpen ? (*/}
                    {/*<FormattedDatePicker*/}
                      {/*allowClear={false}*/}
                      {/*dateValue={expected_inprocess_date}*/}
                      {/*update={(e) => this.changeExpectedIPDate(e)}*/}
                    {/*/>*/}
                  {/*) : (*/}
                    {/*<p*/}
                      {/*className="panel-info-value client-name"*/}
                      {/*onClick={(e) => this.showDatePicker(e)}*/}
                    {/*>*/}
                      {/*+ add date*/}
                    {/*</p>*/}
                  {/*)}*/}
                {/*</div>*/}
              {/*</Col>*/}
              <Col md="3">
                <div>
                  <p className="small-title deal-info-title">Property Type</p>
                  <p className="panel-info-value">{property_type || "N/A"}</p>
                </div>
              </Col>
              <Col md="3">
                <div>
                  <p className="small-title deal-info-title">Loan Amount</p>
                  <p className="panel-info-value">{loan_amount || "N/A"}</p>
                </div>
              </Col>
            </Row>
            <Row className="brokers-container">
              <p className="small-title deal-info-title">Brokers</p>
              <p className="bold-text">{originators}</p>
              <div className="brokers-list">
                <p className="xs-text">
                  {brokersList.length > 150 && !this.state.showMoreBrokers
                    ? brokersList.substring(
                        0,
                        brokersList.substring(0, 150).lastIndexOf(", ")
                      ) + "..."
                    : brokersList}
                  {brokersList.length > 150 ? (
                    <span
                      className="orange-text"
                      onClick={(e) => this.showMoreBrokersToggle(e)}
                    >
                      {this.state.showMoreBrokers ? "Show Less" : "Show More"}
                    </span>
                  ) : (
                    ""
                  )}
                </p>
              </div>
            </Row>
            <Row className="note-container">
              <Col md="10" key={deal_id}>
                <div>
                  {notes != null ? (
                    <div>
                      <p className="small-title deal-info-title notes-title">
                        <i>
                          {last_notes_by} on {notes_date}
                        </i>
                      </p>
                      <p className="note">{notes}</p>
                    </div>
                  ) : (
                    " "
                  )}
                </div>
              </Col>
            </Row>
            {moment(notes_due_date).isValid() ? (
              <Row>
                <p className="small-title notes-title">
                  Remind me about this on
                </p>
                <div className="remind-me">
                  <p>
                    <i className="material-icons">date_range</i>
                    {moment(notes_due_date).format("L")}
                  </p>
                  <p>
                    <i className="material-icons">access_time</i>
                    {moment(notes_due_date).format("LT")}
                  </p>
                </div>
              </Row>
            ) : (
              " "
            )}
            {this.state.modalOpen && (
              <ClientModal
                isOpen={true}
                onRequestClose={this.closeModal}
                onAfterOpen={this.afterOpenModal}
                contact={contact}
                name={full_name}
                key={deal_id}
                noContact={isEmpty(contact)}
                loading={loadingContact}
              />
            )}
            <DealRatingModal
              isOpen={this.state.modalRatingOpen}
              onAfterOpen={this.afterOpenModal}
              deal_rating={deal_rating}
              updateRating={this.updateRating}
            />
            <DealReviewModal
              allReviewed={
                forecast_reviewed && ip_date_reviewed && notes_reviewed
              }
              forecast={deal_rating}
              forecast_reviewed={forecast_reviewed}
              ip_date={expected_inprocess_date}
              ip_date_reviewed={ip_date_reviewed}
              notes={notes}
              notes_reviewed={notes_reviewed}
              isOpen={this.state.modalReviewOpen}
              onRequestClose={this.closeReviewModal}
              updateDealReview={this.updateDealReview}
            />
            <Divider />
            <Row className="note-options">
              {deal_status !== DealStatusType.CL && (
                <p
                  className="orange-text xs-text inline-block"
                  onClick={(e) =>
                    this.props.addDealNoteClick(
                      "FPW",
                      deal_id,
                      total_violations,
                      additional_properties,
                      e
                    )
                  }
                >
                  <i className="material-icons icon-left medium-text middle">
                    add
                  </i>
                  <i>Add Note</i>
                </p>
              )}
              <div
                className="view-all"
                onClick={(e) => this.dealClick(e, "notes")}
              >
                <p>View All Notes</p>
                <p>{note_count}</p>
              </div>
            </Row>
            <Divider />
          </Row>
          <Row className="comps-row">
            <div
              onClick={(e) =>
                this.props.compsClick(
                  inNYC,
                  property_address,
                  city_state,
                  property_type_id,
                  e
                )
              }
            >
              <div className={total_violations !== null ? "border-right" : ""}>
                <p className="violation-value comps">
                  {formatTotalComps(TotalComps)}
                  <span>Comps</span>
                </p>
              </div>
            </div>
            <div
              className={
                total_violations !== null ? "comps-row-violations" : "hidden"
              }
              onClick={(e) => this.dealClick(e, "violations")}
            >
              <div>
                <p className="violation-value">
                  {total_violations}
                  <span>Violations</span>
                </p>
              </div>
              <div>
                <p className="violation-value">
                  {total_complaints}
                  <span>Complaints</span>
                </p>
              </div>
            </div>
          </Row>
        </div>
      </div>
    );
  }
}

export default FPWDeal;
