import React from "react";
import Modal from "react-modal";
import Form from "muicss/lib/react/form";
import Button from "muicss/lib/react/button";

class DealRatingModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      rating: this.props.deal_rating,
    };
    this.onRadioChange = this.onRadioChange.bind(this);
  }

  onRadioChange(e) {
    this.setState({
      rating: e.currentTarget.value ? parseInt(e.currentTarget.value, 10) : 0,
    });
  }

  render() {
    const customStyles = {
      content: {
        margin: "auto",
      },
    };

    return (
      <Modal
        className="deal-rating-modal"
        ariaHideApp={false}
        contentLabel="Modal"
        isOpen={this.props.isOpen}
        onRequestClose={(e) => this.props.updateRating(this.state.rating, e)}
        onAfterOpen={this.props.onAfterOpen}
        style={customStyles}
        shouldCloseOnOverlayClick={true}
      >
        <Form className="deal-rating-modal-form">
          <i
            className="material-icons modal-exit"
            onClick={(e) => this.props.updateRating(this.state.rating, e)}
          >
            close
          </i>
          <h4 className="change-rating-header">Forecast</h4>
          <div className="deal-ratings-container">
            <input
              className="radio-input"
              type="radio"
              name="1"
              id="radio1"
              checked={this.state.rating === 1}
              onChange={this.onRadioChange}
              value={1}
            />
            <label className="radio-label label-one" htmlFor="radio1">
              1
            </label>
            <input
              className="radio-input"
              type="radio"
              name="2"
              id="radio2"
              checked={this.state.rating === 2}
              onChange={this.onRadioChange}
              value={2}
            />
            <label className="radio-label label-two" htmlFor="radio2">
              2
            </label>
            <input
              className="radio-input"
              type="radio"
              name="3"
              id="radio3"
              checked={this.state.rating === 3}
              onChange={this.onRadioChange}
              value={3}
            />
            <label className="radio-label label-three" htmlFor="radio3">
              3
            </label>
          </div>
          <div>
            <Button
              variant="flat"
              className="deal-rating-modal-done"
              onClick={(e) => this.props.updateRating(this.state.rating, e)}
            >
              Done
            </Button>
          </div>
        </Form>
      </Modal>
    );
  }
}

export default DealRatingModal;
